
.table-wrap {
  position: relative;
  width: 100%;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  background: #fff;
}
.table-wrap table {
  font-size: 14px;
  background-color: #fff;
  text-align: center;
  border-top: 1px solid #e7e7eb;
  border-bottom: 1px solid #e7e7eb;
  border-spacing: 0;
}
.ms-table-body {
  position: relative;
  overflow-y: scroll;
}
.ms-table-body .table_cell {
  border-top: 1px solid #e7e7eb;
  border-right: 1px solid #e7e7eb;
}
.ms-table-body table {
  border-top: none;
}
.table_cell {
  padding: 10px 8px;
  min-width: 200px;
}
.ms-table-sort {
  display: inline-block;
  position: relative;
  width: 10px;
  height: 12px;
  margin-left: 4px;
  margin-top: -1px;
  vertical-align: middle;
  overflow: hidden;
  cursor: pointer;
}
.ms-table-sort i {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  color: #bbbec4;
  transition: color 0.2s ease-in-out;
}
.ms-table-sort i.cur {
  border-color: transparent transparent #2d8cf0 transparent;
}
.ms-table-sort i:first-child {
  top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #bbbec4 transparent;
}
.ms-table-sort i:last-child {
  bottom: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #bbbec4 transparent transparent transparent;
}
.ms-table-sort i.cur:first-child {
  border-bottom-color: #2d8cf0;
}
.ms-table-sort i.cur:last-child {
  border-top-color: #2d8cf0;
}
.empty_data {
  padding: 100px 0;
  text-align: center;
  font-size: 14px;
  color: #8d8d8d;
}



