
.ms-organization-wrap {
  position: relative;
}
.ms-organization-title{
  width: 300px;
  height: 38px;
  line-height: 28px;
  padding: 5px 10px;
  border: 1px solid #d6c6c6;
  border-radius: 3px;
}
.ms-organization-body {
  position: absolute;
  top: 50px;
  left: 0px;
  z-index: 100;
  width: 300px;
  height: 300px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: hidden;
  box-shadow: 0 5px 5px -3px rgba(0,0,0,.2);
}
.ms-organization-body-header {
  position: relative;
}
.ms-organization-search {
  padding: 10px;
}
.ms-organization-search input {
  width: 100%;
  height: 34px;
  padding: 5px;
  outline: none;
}
.ms-organization-router {
  line-height: 20px;
  padding: 5px 10px;
  width: 100%;
  overflow-x: scroll;
}
.ms-organization-router a {
  cursor: pointer;
}
.ms-organization-body-top {
  bottom: 60px;
}
.ms-organization-content {
  position: relative;
  /*padding-top: 30px;*/
}
.ms-organization-options {
  list-style: none;
  padding: 15px 0px;
  overflow-y: scroll;
  height: 215px;
}
.ms-organization-options li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}
.ms-organization-options li:hover{
  background: rgba(0,0,0,.12);
}
.ms-organization-options li.active{
  color: #fff;
  background-color: #5FB878;
}
.ms-organization-body-with-search .ms-organization-body-header{
}
.ms-organization-body-with-search .ms-organization-content{
}



